export default function NetworkStatus() {
  const { ip, port } = useWebSocketConfig();
  const { setInitialState } = useModel('@@initialState');

  // 使用滚动重置Hook
  useScrollReset();

  const [group, setGroup] = useState('Quectel_AT');
  let lastStartTime = 0;
  let initUp = 0;
  let initDown = 0;

  // 主动连接WebSocket并检测命令族
  useEffect(() => {
    if (ip && port && wsService.getStatus() !== 'open') {
      wsService.connect(`ws://${ip}:${port}`);
    }
    const setTitle = async () => {
      await ensureGroupReady(ip, port);
      const atiRaw = await safeSendAT('ATI');
      const { model } = parseATISystemInfo(atiRaw);
      setInitialState((prev: any) => ({
        ...prev,
        dynamicTitle: model ? `${model}` : 'Modem-Manager',
      }));
    };
    setTitle();
    return () => {
      wsService.removeOnOpenCallback(setTitle);
    };
  }, [ip, port]);

  return (
    <Row gutter={[16, 16]} align="stretch">
      <Col xs={24} md={24}>
        <NetworkInfoCard />
      </Col>
      <Col xs={24} md={24}>
        <BasicInfoCard />
      </Col>
      <Col xs={24} md={12}>
        <NetworkSpeedCard />
      </Col>
      <Col xs={24} md={12}>
        <TrafficStatCard lastStartTime={lastStartTime} initUp={initUp} initDown={initDown} />
      </Col>
      <Col xs={24}>
        <TempMonitorCard />
      </Col>
    </Row>
  );
}

function NetworkInfoCard() {
  const [autoRefresh, setAutoRefresh] = useState(false);
  const [refreshInterval, setRefreshInterval] = useState(5);
  const intervalRef = useRef<any>(null);
  // 通过ref操作子组件刷新
  const signalRef = useRef<{ refresh: () => void }>(null);
  const paramRef = useRef<{ refresh: () => void }>(null);
  const aggRef = useRef<{ refresh: () => void }>(null);

  useEffect(() => {
    if (!autoRefresh) {
      if (intervalRef.current) clearInterval(intervalRef.current);
      return;
    }
    intervalRef.current = setInterval(() => {
      signalRef.current?.refresh?.();
      paramRef.current?.refresh?.();
      aggRef.current?.refresh?.();
    }, refreshInterval * 1000);
    return () => { if (intervalRef.current) clearInterval(intervalRef.current); };
  }, [autoRefresh, refreshInterval]);

  return (
    <Card className="my-card" style={{}}
      title={
        <span style={{ display: 'flex', alignItems: 'center' }}>
          <span style={{ lineHeight: '32px' }}>网络信息</span>
          <span
            style={{
              background: 'var(--ant-color-fill-tertiary)',
              borderRadius: 2,
              padding: '0 8px',
              height: 24,
              display: 'inline-flex',
              alignItems: 'center',
              marginLeft: 12,
              fontSize: 12,
              color: 'var(--ant-color-text-secondary)',
              fontWeight: 'normal',
              lineHeight: '32px',
            }}
          >
            展示当前网络的各项关键指标
          </span>
        </span>
      }
      extra={
        !autoRefresh ? (
          <Button
            type="default"
            style={{ borderRadius: 6, padding: '0 18px', height: 32, fontSize: 14, fontWeight: 500, marginLeft: 8, borderColor: 'var(--ant-color-primary)', color: 'var(--ant-color-primary)' }}
            onClick={() => setAutoRefresh(true)}
          >
            自动刷新
          </Button>
        ) : (
          <button
            style={{
              display: 'inline-flex',
              alignItems: 'center',
              border: '1px solid var(--ant-color-primary)',
              borderRadius: 6,
              padding: '0 12px',
              height: 32,
              background: 'var(--ant-color-info-bg)',
              boxSizing: 'border-box',
              userSelect: 'none',
              color: 'var(--ant-color-primary)',
              fontWeight: 500,
              fontSize: 14,
              outline: 'none',
              transition: 'background 0.2s, border 0.2s',
            }}
            type="button"
          >
            <span
              style={{ marginRight: 0, cursor: 'pointer' }}
              onClick={e => { e.stopPropagation(); setAutoRefresh(false); }}
            >
              自动刷新
            </span>
            <InputNumber
              min={2}
              max={60}
              value={refreshInterval}
              onChange={v => setRefreshInterval(Number(v) || 5)}
              onKeyDown={e => {
                if (!['ArrowUp', 'ArrowDown', 'Tab', 'Shift', 'Control', 'Alt'].includes(e.key)) {
                  e.preventDefault();
                }
              }}
              style={{
                width: 52,
                height: '100%',
                border: 'none',
                background: 'transparent',
                color: 'var(--ant-color-primary)',
                fontSize: 14,
                textAlign: 'left',
                margin: '0px',
                outline: 'none',
                boxShadow: 'none',
                lineHeight: '24px',
                padding: 0,
              }}
              className="transparent-input-number"
            />
            <span style={{ marginLeft: 2, lineHeight: '24px' }} onClick={e => { e.stopPropagation(); setAutoRefresh(false); }}>秒</span>
          </button>
        )
      }
    >
      <Row gutter={[12, 12]}>
        <Col xs={24} md={16}>
          <SignalCard ref={signalRef} />
        </Col>
        <Col xs={24} md={8}>
          <NetworkParamCard ref={paramRef} />
        </Col>
        <Col xs={24} md={24}>
          <CarrierAggCard ref={aggRef} />
        </Col>
      </Row>
    </Card>
  );
}

function NetworkSpeedCard() {
  // 共享数据状态
  const { ip, port } = useWebSocketConfig();
  const [showRealtime, setShowRealtime] = useState(false);
  const [showIntervalModal, setShowIntervalModal] = useState(false);
  const [intervalMs, setIntervalMs] = useState(500);
  const [realtimeRate, setRealtimeRate] = useState<{ up: number, down: number } | null>(null);
  const [signedRate, setSignedRate] = useState<any>(null);
  const [operator, setOperator] = useState<string>('');
  const [apn, setApn] = useState<string>('');
  const intervalRef = useRef<any>(null);

  // 查询签约速率和运营商
  const fetchSigned = async () => {
    try {
      await ensureGroupReady(ip, port);
      const realGroup = await getCurrentGroupAsync(ip, port);
      const cmds = getSignedRateCmds(realGroup);
      const results = [];
      for (const cmd of cmds) {
        results.push(await safeSendAT(cmd));
      }
      const info = parseSignedRate(results, realGroup);
      setSignedRate(info);
      setApn(info?.apn || '');
    } catch { }
  };
  const fetchOperator = async () => {
    try {
      await ensureGroupReady(ip, port);
      const realGroup = await getCurrentGroupAsync(ip, port);
      const cmd = getOperatorCmd(realGroup);
      const raw = await safeSendAT(cmd);
      setOperator(parseOperator(raw, realGroup) || '');
    } catch { }
  };
  useEffect(() => {
    let cancel = false;
    const doQuery = () => {
      if (!cancel) {
        fetchSigned();
        fetchOperator();
      }
    };
    if (wsService.getStatus && wsService.getStatus() === 'open') {
      doQuery();
    } else {
      wsService.addOnOpenCallback(doQuery);
    }
    return () => {
      cancel = true;
      wsService.removeOnOpenCallback(doQuery);
    };
    // eslint-disable-next-line react-hooks/exhaustive-deps
  }, [ip, port]);

  // 实时速率定时刷新
  useEffect(() => {
    if (!showRealtime) {
      setRealtimeRate(null);
      if (intervalRef.current) clearInterval(intervalRef.current);
      return;
    }
    async function fetchRealtime() {
      try {
        await ensureGroupReady(ip, port);
        const realGroup = await getCurrentGroupAsync(ip, port);
        const cmd = getRealtimeRateCmd(realGroup);
        const raw = await safeSendAT(cmd);
        const info = parseRealtimeRate(raw, realGroup);
        if (info) setRealtimeRate(info);
      } catch { }
    }
    fetchRealtime();
    intervalRef.current = setInterval(fetchRealtime, intervalMs);
    return () => { if (intervalRef.current) clearInterval(intervalRef.current); };
  }, [showRealtime, intervalMs, ip, port]);

  return (
    <Card
      title={
        <>
          <span>网络速率信息</span>
          <span
            style={{
              background: 'var(--ant-color-fill-tertiary)',
              borderRadius: 2,
              padding: '0 8px',
              height: 24,
              display: 'inline-flex',
              alignItems: 'center',
              marginLeft: 12,
              fontSize: 12,
              color: 'var(--ant-color-text-secondary)',
              fontWeight: 'normal',
              lineHeight: '32px',
            }}
          >
            展示网络速率相关信息
          </span>
        </>
      }
      extra={<Switch checked={showRealtime} onChange={v => {
        if (v) setShowIntervalModal(true); else setShowRealtime(false);
      }} checkedChildren="实时网速开启" unCheckedChildren="实时网速关闭" />}
      style={{ marginTop: 0 }}
      styles={{ body: { padding: 24 } }}
    >
      <RealtimeSpeedCard showRealtime={showRealtime} realtimeRate={realtimeRate} onSwitch={v => { if (v) setShowIntervalModal(true); else setShowRealtime(false); }} />
      <CurrentNetworkCard signedRate={signedRate} operator={operator} apn={apn} />
      {/* 刷新间隔弹窗 */}
      <Modal
        title="主动刷新时间"
        open={showIntervalModal}
        onCancel={() => { setShowIntervalModal(false); setShowRealtime(false); }}
        onOk={() => { setShowIntervalModal(false); setShowRealtime(true); }}
        okText="确定"
        cancelText="取消"
      >
        <div style={{ marginBottom: 16 }}>主动刷新时间（200-65535ms）：</div>
        <InputNumber min={200} max={65535} value={intervalMs} onChange={v => setIntervalMs(Number(v) || 200)} addonAfter="ms" style={{ width: 200 }} />
        <div style={{ marginTop: 12, color: 'var(--ant-color-text-secondary)', fontSize: 14 }}>说明：间隔越小，数据更新越频繁，但系统负担越大</div>
      </Modal>
    </Card>
  );
}



